<template>
  <div>
    <h1>Order now</h1>
    <hr />
    <div v-if="empty" class="alert alert-warning">Your cart is empty</div>
    <template v-else>
      <table class="table table-bordered">
        <thead>
          <tr>
            <th>Title</th>
            <th>Price</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(product, index) in products" :key="index">
            <td>{{ product.title}}</td>
            <td>{{ product.price}}</td>
          </tr>
        </tbody>
      </table>
      <div class="alert alert-primary">Total: {{total}}</div>

      <app-form v-if="getForm !== 'done' "></app-form>
      <app-result v-else></app-result>
    </template>
  </div>
</template>
<script>
import AppForm from "./Form";
import AppResult from "./Result";

import { mapGetters, mapActions, mapMutations } from "vuex";
export default {
  computed: {
    ...mapGetters("products", {
      productsAll: "items"
    }),
    ...mapGetters("form", {
      getForm: "form"
    }),
    ...mapGetters("cart", {
      productsInCart: "products"
    }),

    products() {
      //  вывод продукта у которого айди равно айди из корзины
      return this.productsAll.filter(elem => {
        return this.productsInCart.indexOf(elem.id) !== -1;
      });
    },
    empty() {
      return this.products.length === 0;
    },
    total() {
      let sum = 0;

      this.products.forEach(element => {
        sum += element.price;
      });
      return sum;
    }
  },
  methods: {
    ...mapActions("cart", {
      clearCart: "clear"
    })
  },
  components: {
    AppForm,
    AppResult
  }
};
</script>


