<template>
  <div>
    <!-- <h1>{{ products.title}}</h1> -->
    <!-- <router-link to="/">Back to prooducts</router-link> -->
    <hr />
    <div class="product">
      <div class="product__image">
        <v-carousel :carousel_data="sliderItems"></v-carousel>
      </div>
      <div class="product__content">
        <div class="product__title">{{product.title}}</div>
        <div class="product__category">{{product.category.name}}</div>
        <div class="product__description">{{ product.description }}</div>
        <div class="product__likes">
          <button class="product__like" @click="likeThis(product.id)">
            <i class="far fa-heart"></i>
          </button>
          <button class="product__like" @click="unLikeThis(product.id)">
            <i class="fas fa-heart"></i>
          </button>
          {{ product.likes.count }}
        </div>
        <div class="product__price">{{ product.price.text }}</div>
        <div class="product__view">{{ product.views_count}}</div>
        <div class="product__btn">
          <button
            class="btn btn-primary"
            v-if="inCart.indexOf(product.id) === - 1"
            @click="addToCart(product.id)"
          >В корзину</button>
          <button v-else class="btn btn-warning" @click="removeFromCart(product.id)">В корзине</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import vCarousel from "./Carousel";
import { mapGetters, mapActions } from "vuex";
export default {
  computed: {
    ...mapGetters("cart", {
      inCart: "products"
    }),
    id() {
      // прааметры переданные по ссылке в роутере
      return this.$route.params.id;
    },
    product() {
      // передаем геттеру параметр
      return this.$store.getters["products/item"](this.id);
    },
    sliderItems() {
      return this.product.photos;
    }
  },
  methods: {
    ...mapActions("cart", {
      addToCart: "add",
      removeFromCart: "remove"
    })
  },
  components: {
    vCarousel
  }
};
</script> 
<style  scoped>
.product {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.product__image {
  flex-basis: 50%;
}
.product__content {
  /* margin-left: 60px; */
  flex-basis: 50%;
}
.product__title {
  font-size: 50px;
  font-weight: 500;
}
.product__category {
  text-align: right;
  color: #acacac;
}
.product__description {
  margin-top: 60px;
}
.product__like {
  outline: none;
  border: none;
  background: none;
  font-size: 30px;
  color: #ff3347;
}
.product__likes {
}
.product__price {
}
.product__view {
}
.product__btn {
}
.btn {
}
.btn-primary {
}
.btn-warning {
}
@media (max-width: 1200px) {
  .product__image {
    /* flex-basis: 100%; */
    margin: 0 auto;
  }
  .product__content {
    flex-basis: 100%;
  }
}
</style>


