<template>
  <div>
    <h1>Products</h1>

    <div class="card-deck">
      <div class="card" v-for="item in productsList.response.items" :key="item.id">
        <img :src="item.thumb_photo" class="card-img-top" alt />
        <div class="card-body">
          <router-link :to="/product/ + item.id" tag="h5" class="card-title">
            <a>{{ item.title }}</a>
          </router-link>

          <p class="card-text">{{item.price.text}}</p>
          <button
            class="btn btn-primary"
            v-if="inCart.indexOf(item.id) === - 1"
            @click="addToCart(item.id)"
          >В корзину</button>
          <button v-else class="btn btn-warning" @click="removeFromCart(item.id)">В корзине</button>

          <p class="card-text">
            <small class="text-muted">{{item.category.name}}</small>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import { mapActions } from "vuex";
export default {
  // created() {
  //   this.$store.dispatch("products/loadItems");
  // },
  computed: {
    ...mapGetters("products", {
      productsList: "items"
    }),
    ...mapGetters("cart", {
      inCart: "products"
    })
  },
  methods: {
    ...mapActions("cart", {
      addToCart: "add",
      removeFromCart: "remove"
    })
  }
};
</script>
<style scoped>
a {
  text-decoration: none;
  color: #000;
}
.row {
  padding-left: 15px;
}
</style>


