export default {
  // дает возможность -> $store.state['menu/item']
  namespaced: true,
  state: {
    items: [
      {
        url: '/products',
        text: 'Products List'
      },
      {
        url: '/cart',
        text: 'Cart'
      },
      {
        url: '/checkout',
        text: 'Checkout'
      },
    ]
  },
  getters: {
    items(state) {
      return state.items;
    }
  },
  mutation: {

  },
  actions: {

  }
}


