<template>
  <div>
    <h1>Cart</h1>
    <hr />
    <div v-if="empty" class="alert alert-warning">Your cart is empty</div>
    <template v-else>
      <table class="table table-bordered">
        <thead>
          <tr>
            <th>Title</th>
            <th>Price</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(product, index) in products" :key="index">
            <td>{{ product.title}}</td>
            <td>{{ product.price}}</td>
          </tr>
        </tbody>
      </table>
      <!-- <router-link>
        
      </router-link>-->
      <button class="btn btn-success" @click="onOrder">Order now</button>
      <button class="btn btn-secondary" @click="clearCart">Clear cart</button>
    </template>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  computed: {
    ...mapGetters("products", {
      productsAll: "items"
    }),
    ...mapGetters("cart", {
      productsInCart: "products"
    }),
    products() {
      //  вывод продукта у которого айди равно айди из корзины
      return this.productsAll.filter(elem => {
        return this.productsInCart.indexOf(elem.id) !== -1;
      });
    },
    empty() {
      return this.products.length === 0;
    }
  },
  methods: {
    ...mapActions("cart", {
      clearCart: "clear"
    }),
    onOrder() {
      // перехррд не тольок по route-link
      this.$router.push("/checkout");
    }
  }
};
</script>


