<template>
  <div>
    <header>
      <div class="container">
        <div class="row">
          <div class="col col-sm-9">
            <h1>Site</h1>
          </div>
          <div class="col col-sm-3">
            <div class="alert alert-default">
              <div>In Cart: {{ countInCart }}</div>
            </div>
          </div>
        </div>
        <hr />
      </div>
    </header>
    <section>
      <div class="container">
        <div class="col col-sm-12 menu">
          <ul class="nav nav-pills nav-justified">
            <li class="nav-item" v-for="(item, index) in menuList" :key="index">
              <router-link
                tag="a"
                class="nav-link"
                active-class="active"
                :to="item.url"
              >{{ item.text }}</router-link>
            </li>
          </ul>
        </div>
        <div class="col col-sm-12 pages">
          <!-- mode:out-in - что бы новый объект появлялся только посе завершения другого -->
          <transition name="page" mode="out-in">
            <!-- вывод компонента через роутер -->
            <router-view></router-view>
          </transition>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    // указываем в первом параметре модуль а во втором объект с названием для этого компонениета и название из модуля
    ...mapGetters("menu", {
      menuList: "items"
    }),
    ...mapGetters("cart", {
      countInCart: "count"
    })
  },
  components: {}
};
</script>
<style scoped>
.nav-link {
  transition: all 0.3s;
}
a {
  color: #000;
  text-decoration: none;
  font-weight: 500;
}
.active a {
  color: white;
}
a .nav-link {
}
.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  background: #a0c601;
  color: #ededed;
}
.list-group-item {
  transition: 0.5s;
}
.pages {
  overflow: hidden;
}
.page-enter {
}
.page-enter-active {
  animation: slideLeft 0.5s;
}
.page-enter-to {
}
.page-leave {
}
.page-leave-active {
  opacity: 0;
  transition: opacity 0.5s;
}
.page-leave-to {
}

@keyframes slideLeft {
  from {
    transform: translateX(600px);
  }
  to {
    transform: translateX(0px);
  }
}
</style>


