export default {
  namespaced: true,
  state: {
    products: []
  },
  getters: {
    products(state) {
      return state.products;
    },
    count(state) {
      return state.products.length;
    }
  },
  mutations: {
    // при размерах и сумме передавать объект id а объект
    add(state, id) {
      // добавляетя id в массив продуктов
      // проверка есть ли  в масиве уже товар
      if (state.products.indexOf(id) === -1) {
        state.products.push(id);
      }
    },
    remove(state, id) {
      let pos = state.products.indexOf(id)
      if (pos !== -1) {
        state.products.splice(pos, 1);
      }
    },
    clear(state) {
      state.products = [];
    }

  },
  actions: {
    add(store, id) {
      store.commit('add', id);
    },
    remove(store, id) {
      store.commit('remove', id);
    },
    clear(store) {
      store.commit('clear');
    }
  }
}


