import Vue from 'vue';

export default {
  namespaced: true,
  state: {
    items: []
  },
  getters: {
    items(state) {
      return state.items;
    },
    item: (state) => (id) => {
      for (let i = 0; i < state.items.response.items.length; i++) {
        if (state.items.response.items[i].id == id) {
          return state.items.response.items[i];
        }
      }


    }
  },
  mutations: {
    loadItems(state, data) {
      state.items = data;
    }
  },
  actions: {
    //     access_token: "1f99f6c95512c5fd2c0f77638c46234365efc015e4f77f78d40073f01155980cb5cc17881681929dcb568",
    // expires_in: 0,
    // user_id: 86968931
    // }
    loadItems(store) {
      // получаем данные из api 
      Vue.http.get(`market.get?owner_id=-195835837&extended=1&access_token=1f99f6c95512c5fd2c0f77638c46234365efc015e4f77f78d40073f01155980cb5cc17881681929dcb568&v=5.107`)
        .then(response => response.json())
        .then(data => {
          store.commit('loadItems', data);

        });
    }
  }
}




