import Vue from 'vue'
import App from './App.vue'

import { store } from './store'

import { router } from './routes'

// для api 
import VueResource from 'vue-resource';
Vue.use(VueResource);
// Vue.http.post 
// Vue.http.get
Vue.http.options.root = 'https://api.vk.com/method/';

new Vue({
  el: '#app',
  store,
  router,
  render: h => h(App)
})


