<template>
  <div class="form-group">
    <label>
      {{ name }}
      <i v-if="activated" class="fas" :class="validClass"></i>
    </label>
    <input type="text" :value="value" @input="onInput" class="form-control" />
  </div>
</template>
<script>
export default {
  props: {
    name: null,
    value: null,
    pattern: null
  },
  data() {
    return {
      activated: this.value !== ""
    };
  },
  computed: {
    validClass() {
      return this.pattern.test(this.value)
        ? "fa-check-circle animated pulse "
        : "fa-exclamation-circle animated tada";
    }
  },
  methods: {
    onInput(e) {
      this.activated = true;
      this.$emit("changedata", {
        value: e.target.value,
        valid: this.pattern.test(e.target.value)
      });
    }
  }
};
</script>
<style scoped>
.fa-check-circle {
  color: green;
}
.fa-exclamation-circle {
  color: red;
}
</style>


