<template>
  <div>
    <div class="wrapper">
      <div class="sample">
        <!-- <i class="fas fa-exclamation-circle"></i>
        <i class="fas fa-check-circle"></i>-->
        <form @submit.prevent="formSubmited = true">
          <app-input
            v-for="(item, index) in getInputs"
            :key="index"
            :name="item.name"
            :value="item.value"
            :pattern="item.pattern"
            @changedata="onChange(index, $event)"
          ></app-input>
          <button
            :disabled="done !== getInputs.length"
            type="submit"
            @click="submited"
            class="btn btn-primary"
          >
            {{getForm}}
            <i v-if="getForm == 'loading'" class="fas fa-spinner"></i>
          </button>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import AppInput from "./Input";
import AppResult from "./Result";

import { mapGetters, mapMutations } from "vuex";
export default {
  data() {
    return {
      done: 0,
      controls: []
    };
  },
  created() {
    for (let i = 0; i < this.getInputs.length; i++) {
      this.controls.push(false);
    }
    // console.log(this.inputs);
  },
  computed: {
    ...mapGetters("products", {
      productsAll: "items"
    }),
    ...mapGetters("cart", {
      productsInCart: "products"
    }),
    ...mapGetters("form", {
      getInputs: "inputs",
      getForm: "form"
    }),
    products() {
      //  вывод продукта у которого айди равно айди из корзины
      return this.productsAll.filter(elem => {
        return this.productsInCart.indexOf(elem.id) !== -1;
      });
    },
    progressWidth() {
      return {
        width: (100 / this.getInputs.length) * this.done + "%"
      };
    }
  },
  methods: {
    ...mapMutations("form", {
      pushProductsInForm: "pushProducts"
    }),
    onChange(index, e) {
      this.$store.commit("form/changeValue", {
        index: index,
        value: e.value
      });
      this.controls[index] = e.valid;
      let done = 0;
      for (let i = 0; i < this.controls.length; i++) {
        if (this.controls[i]) {
          done++;
        }
      }
      // console.log(done, this.inputs.length);
      this.done = done;
      // console.log(index, e.value, e.valid);
    },
    submited() {
      this.pushProductsInForm(this.products);
      this.$store.dispatch("form/sendForm");
    }
  },
  components: {
    AppInput,
    AppResult
  }
};
</script>
<style scoped>
.fa-spinner {
  animation: flip 1s linear infinite;
}
@keyframes flip {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>


