export default {
  namespaced: true,
  state: {
    inputs: [
      {
        name: "Name",
        value: "",
        pattern: /^[a-zA-Z]{2,30}$/
      },
      {
        name: "Phone",
        value: "",
        pattern: /^[0-9]{7,14}$/
      },
      {
        name: "Email",
        value: "",
        pattern: /.+/ //  /.+@.+\..+/
      }
    ],
    products: [],
    form: 'Send Data',
    messege: []
  },
  getters: {
    name(state) {
      return state.inputs[0].value;
    },
    inputs(state) {
      return state.inputs;
    },
    form(state) {
      return state.form;
    },
    getMessage(state) {
      return state.messege;
    }

  },
  mutations: {
    changeValue(state, payload) {
      state.inputs[payload.index].value = payload.value;
    },
    formSend(state) {
      state.form = 'loading';
      for (let i = 0; i < state.inputs.length; i++) {
        state.messege.push(state.inputs[i].value);

      }
      state.messege.push(state.products);

    },
    formDone(state) {

      state.form = 'done';
    },
    pushProducts(state, data) {
      state.products.push(data);
    }
  },
  actions: {

    sendForm(store) {
      store.commit('formSend');

      setTimeout(() => {
        store.commit('formDone')
      }, 1000);
    }
  },
}


