<template>
  <div class="wrapper" :style="getWidthForWrapper">
    <div class="v-carousel" :style="{'left': '-' + (100 * this.currentSlideIndex)  + '%'}">
      <v-carousel-item v-for="item in carousel_data" :key="item.id" :item_data="item.sizes[7].url"></v-carousel-item>
    </div>
    <template v-if="isNotEmpty">
      <button @click="prevSlide">prev</button>
      <button @click="nextSlide">next</button>
    </template>
  </div>
</template>
<script>
import vCarouselItem from "./Carousel-item";
export default {
  name: "v-carousel",
  props: {
    carousel_data: null,
    interval: {
      type: Number,
      default: () => 0
    }
  },
  mounted() {
    if (this.interval > 0) {
      setInterval(() => {
        this.nextSlide();
      }, this.interval * 1000);
    }
  },
  data() {
    return {
      currentSlideIndex: 0
    };
  },
  computed: {
    isNotEmpty() {
      if (this.carousel_data.length > 1) {
        return true;
      }
    },
    getWidthForWrapper() {
      return {
        "max-width": this.carousel_data[0].sizes[7].width + "px"
      };
    }
  },
  methods: {
    prevSlide() {
      if (this.currentSlideIndex > 0) {
        this.currentSlideIndex--;
      } else {
        this.currentSlideIndex = this.carousel_data.length - 1;
      }
    },
    nextSlide() {
      if (this.carousel_data.length - 1 == this.currentSlideIndex) {
        this.currentSlideIndex = 0;
      } else {
        this.currentSlideIndex++;
      }
    }
  },
  components: {
    vCarouselItem
  }
};
</script>
<style  scoped>
.wrapper {
  overflow: hidden;
  /* margin: 0 auto; */
}
.v-carousel {
  transition: left ease-out 0.3s;
  display: flex;
  position: relative;
}
</style>


