import Vue from 'vue'
import VueRouter from 'vue-router'

// объявляем исползование роутера
Vue.use(VueRouter);
//
// импортируем компоненты которые нужны
import ProductList from './components/ProductList';
import Cart from './components/Cart';
import Product from './components/Product';
import E404 from './components/E404';
import Checkout from './components/Checkout';

// для лиспатча к складу
import { store } from './store';

// констатна с рутами(массив)
const routes = [
  // роут это объект с путем и названием компонента

  {
    path: '/',
    // переадресация на другой путь
    redirect: { name: 'products' }

  },
  {
    // уникальное название роутера
    name: 'products',
    path: '/products',
    component: ProductList,
    // создается до того как началась анимация
    beforeEnter(form, to, next) {
      store.dispatch("products/loadItems");
      next();
    }
  },
  {
    // для передачи парамтера используй :
    path: '/product/:id',
    component: Product
  },
  {
    path: '/cart',
    component: Cart
  },
  {
    path: '/checkout',
    component: Checkout
  },
  {
    path: '*',
    component: E404
  },
];
//экспортируем экземляр
export const router = new VueRouter({
  routes,
  mode: 'history'
});


