<template>
  <table class="table table-bordered">
    <tr v-for="(item,index) in getInputs" :key="index">
      <td>{{item.name}}</td>
      <td>{{item.value}}</td>
    </tr>
    <tr>
      <td>{{getMessege}}</td>
    </tr>
  </table>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters("form", {
      getInputs: "inputs",
      getMessege: "getMessage"
    })
  }
};
</script>


